import { gql } from "../__generated__";
import { useQuery } from '@apollo/client';

const ALL_STUFF =  gql(`query getEventTimetable(
    $orderBy: eventPhaseOrderByEnum
    $orderDirection: OrderDirectionEnum
    $eventId: Int
  ) {
    getEventTimetable(
      orderBy: $orderBy
      orderDirection: $orderDirection
      eventId: $eventId
    ) {
      id
      phaseName
      eventID
      phaseDateAndTime
      sexCode
      unitName
      unitTypeName
      discipline {
        _id
        name
        typeName
        typeOrder
        order
        nameUrlSlug
        __typename
      }
      isStartlistPublished
      isResultPublished
      isPhaseSummaryPublished
      combinedDisciplineOrder
      phaseOrder
      phaseSessionName
      phaseSessionOrder
      status
      isPointsPublished
      phaseNameUrlSlug
      sexNameUrlSlug
      __typename
    }
  }
`);

export const GetCompetitions = () => {
    const { loading, data } = useQuery(ALL_STUFF, { variables: { eventId: 7138987  } });

    if (loading) {
        return <p>Laddar...</p>
    }

    if (!data) {
        return <p>Err...</p>
    }

    return data?.getEventTimetable?.map((event) => {
        return <>
        <h1>{event?.discipline?.name} - {event?.phaseName}</h1>
        <p>{event?.phaseDateAndTime}</p>
        </>
    })

};
